import { MyContext } from "ContextData";
import { Button } from "components";
import { Form } from "components/Form";
import React, { useContext } from "react";
import "./style.css";

export const Register = () => {
  const {
    showRegistrationData,
    onChangeName,
    onChangeSubName,
    onChangeEmail,
    onChangeNewPassword,
    name,
    subName,
    email,
    newPassword,
    registerData,
    onChangeGender,
    gender,
    onChangeChecked,
    checked,
  } = useContext(MyContext);

  const {
    registerName,
    registerSubName,
    registerEmail,
    registerPassword,
    registerGender,
  } = registerData;

  return (
    <form className="wrapper">
      <h3> </h3>
      <Form
        placeholder="  "
        name="name"
        value={name}
        onChange={onChangeName}
        type="text"
      />
      <Form
        placeholder="  "
        name="name"
        value={subName}
        onChange={onChangeSubName}
        type="text"
      />
      <Form
        placeholder="  "
        name="name"
        value={email}
        onChange={onChangeEmail}
        type="email"
      />
      <Form
        placeholder="  "
        name="name"
        value={newPassword}
        onChange={onChangeNewPassword}
        type="password"
      />
      <label>
        <select value={gender} onChange={onChangeGender}>
          <option value="man"></option>
          <option value="wooman"></option>
        </select>
      </label>
      <label>
          
        <input type="checkbox" checked={checked} onChange={onChangeChecked} className="input-checkbox"/>
      </label>

      <Button text="" onClick={showRegistrationData} />
      {registerName && registerSubName && registerEmail && registerPassword ? (
        <div>
          <h3>   </h3>
          <ul>
            <li> : {registerName}</li>
            <li> : {registerSubName}</li>
            <li> : {registerEmail}</li>
            <li> : {registerPassword}</li>
            <li> : {registerGender === "man" ? "" : ""}</li>
          </ul>
        </div>
      ) : (
        <></>
      )}
    </form>
  );
};
